/**
 * @fileoverview This file defines the server-side actions for the application.
 * Server Actions are asynchronous functions that are executed on the server.
 * They can be called from Server and Client Components to handle form submissions
 * and data mutations, providing a secure way to interact with the database.
 */
'use server';

import { query } from '@/lib/db';

/**
 * Authenticates a user based on an identifier (email or employee ID) and password.
 * This function checks against the hr_personnel, admins, and employees tables.
 * @param {string} identifier - The user's email or employee ID.
 * @param {string} password - The user's password.
 * @returns {Promise<{success: boolean, message: string, user?: object, redirectUrl?: string}>}
 */
export async function authenticateUser(identifier, password) {
  try {
    // Check for HR or Admin first (if identifier is an email)
    if (identifier.includes('@')) {
      // 1. Check HR personnel table
      const hrResult = await query('SELECT * FROM hr_personnel WHERE email = $1', [identifier]);
      if (hrResult.rows.length > 0) {
        const user = hrResult.rows[0];
        if (user.password && password === user.password) {
          const responseUser = { id: user.id, name: `${user.first_name} ${user.last_name}`, email: user.email, role: 'hr' };
          return { success: true, message: 'Login successful', user: responseUser, redirectUrl: '/hr/dashboard' };
        } else {
          return { success: false, message: 'Invalid credentials.' };
        }
      }

      // 2. If not in HR, check Admins table
      const adminResult = await query('SELECT * FROM admins WHERE email = $1', [identifier]);
      if (adminResult.rows.length > 0) {
        const user = adminResult.rows[0];
        if (user.password && password === user.password) {
          const responseUser = { id: user.id, name: `${user.first_name} ${user.last_name}`, email: user.email, role: 'admin' };
          return { success: true, message: 'Login successful', user: responseUser, redirectUrl: '/admin/dashboard' };
        } else {
          return { success: false, message: 'Invalid credentials.' };
        }
      }
    }

    // 3. If not an email or not found in admin/hr, check employees table
    const employeeResult = await query("SELECT * FROM employees WHERE id = $1 AND status = 'Active'", [identifier]);
    if (employeeResult.rows.length > 0) {
      const user = employeeResult.rows[0];
      if (user.password && password === user.password) {
        const responseUser = { id: user.id, name: `${user.first_name} ${user.last_name}`, role: 'employee' };
        return { success: true, message: 'Login successful', user: responseUser, redirectUrl: '/dashboard' };
      } else {
        return { success: false, message: 'Invalid credentials.' };
      }
    }

    // If no user was found in any table after all checks
    return { success: false, message: 'Invalid credentials.' };

  } catch (error) {
    console.error('Authentication Server Action Error:', error);
    return { success: false, message: 'An internal server error occurred.' };
  }
}

/**
 * Records an employee's clock-in attendance.
 * This function inserts a new attendance record into the database.
 * @param {string} employeeId - The ID of the employee clocking in.
 * @param {string} photoDataUri - A data URI of the photo taken during clock-in.
 * @returns {Promise<{success: boolean, message: string, attendanceRecord?: object}>}
 */
export async function recordClockIn(employeeId, photoDataUri) {
    try {
        if (!employeeId) {
            return { success: false, message: 'Employee ID is required.' };
        }

        const timeIn = new Date();
        const attendanceDate = timeIn.toISOString().split('T')[0];
        const status = 'Present';
        const notes = 'Geofenced clock-in with photo.';

        const insertQuery = `
            INSERT INTO attendance (employee_id, attendance_date, time_in, status, notes)
            VALUES ($1, $2, $3, $4, $5)
            ON CONFLICT (employee_id, attendance_date) 
            DO UPDATE SET time_in = EXCLUDED.time_in, status = EXCLUDED.status, notes = EXCLUDED.notes || ' (Updated)'
            RETURNING *;
        `;

        const result = await query(insertQuery, [employeeId, attendanceDate, timeIn, status, notes]);

        return { success: true, message: 'Clock-in successful!', attendanceRecord: result.rows[0] };

    } catch (error) {
        console.error('Clock-In Server Action Error:', error);
        return { success: false, message: 'An internal server error occurred during clock-in.' };
    }
}
